.myBtn {
    position: absolute;
    right: 20px;
    bottom: 20px;
    padding: 8px 16px;
    background: #5fa36a;
    text-align: center;
    color: #fff;
    font-weight: 600;
    cursor: pointer;
    transition: transform 500ms ease;
    border-radius: 6px;
    border: none;
    font-size: 10px;
    display: flex;
    gap: 5px;
}

.myBtn:hover {
    transform: scale(1.2) translateY(-3px);
}