@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@300;600&family=Roboto:wght@400;700&display=swap');

* {
  margin: 0;
  padding: 0;
}

body {
  background: #fff;
  color: #222;
  font-family: 'Montserrat', sans-serif;
  font-weight: 300;
}

.wrapper {
  width: 1280px;
  margin: 50px auto;
}

header {
  position: relative;
}

header .logo {
  font-weight: 600;
  font-size: 20px;
}

header ul.nav {
  float: right;
  list-style: none;
}

header ul.nav li {
  display: inline;
  margin-left: 25px;
  cursor: pointer;
  transition: opacity 500ms ease;
}

header ul.nav li:hover {
  opacity: 0.5;
}

header .shop_cart_button {
  float: right;
  cursor: pointer;
  transition: color, transform 500ms ease;
}

header .shop_cart_button:hover,
header .shop_cart_button.active {
  color: #5fa36a;
  transform: scale(1.4);
}

header .shop_cart {
  position: absolute;
  top: 30px;
  right: 0;
  width: 450px;
  background: #fafafa;
  border-radius: 10px 10px 0 0;
  box-shadow: 8px 6px 7px 0px rgba(60, 60, 60, 0.43);
  -webkit-box-shadow: 8px 6px 7px 0px rgba(60, 60, 60, 0.43);
  -moz-box-shadow: 8px 6px 7px 0px rgba(60, 60, 60, 0.43);
  z-index: 10;
  padding: 20px 20px 0 20px;
}

header .shop_cart .empty h2 {
  font-size: 20px;
  margin-bottom: 20px;
}


header .shop_cart .item {
  width: 100%;
  float: left;
  margin-bottom: 20px;
}

header .shop_cart .item img {
  width: 70px;
  float: left;
  margin-right: 20px;
}

header .shop_cart .item h2 {
  font-size: 20px;
  margin-bottom: 10px;
}

header .shop_cart .item b {
  color: #797979;
  font-weight: 600;
}

header .shop_cart .item .delet_icon {
  color: #5fa36a;
  float: right;
  position: relative;
  top: -15px;
  cursor: pointer;
  transition: color, transform 500ms ease;
}

header .shop_cart .item .delet_icon:hover {
  color: #06a020;
  transform: scale(1.4);
}

header .shop_cart .summa {
  width: 100%;
  float: left;
  font-weight: 600;
  font-size: 20px;
  margin-bottom: 20px;
}

header .presentation {
  margin: 50px 0;
  background: url('./img/header_img.jpg')no-repeat;
  width: 100%;
  height: 700px;
  background-size: cover;
  background-position: center center;
  background-blend-mode: multiply;
  background-color: #f0f7d1;
  position: relative;
}

header .presentation::after {
  content: 'Оформите свое пространство с нашей мебелью';
  position: absolute;
  top: 100px;
  left: 50px;
  width: 400px;
  font-size: 40px;
  font-weight: 600;
  color: #fff;
}

header .presentation::before {
  content: 'По наилучшим ценам';
  position: absolute;
  top: 250px;
  left: 50px;
  width: 300px;
  font-size: 25px;
  font-weight: 300;
  color: #fff;
}

.categories div {
  display: inline-block;
  background: #f2f2f2;
  border-radius: 50px;
  padding: 10px 20px;
  margin-bottom: 25px;
  margin-right: 15px;
  cursor: pointer;
  border: 1px solid transparent;
  transition: all 500ms ease;
}

.categories div:hover {
  border-color: silver;
  transform: scale(1.1);
}

.full_item {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.7);
  z-index: 999;
  overflow: auto;
}

.full_item>div {
  max-width: 600px;
  position: relative;
  margin: 10% auto;
  padding: 40px 30px;
  background: #fff;
  border-radius: 10px;
}

main {
  display: flex;
  width: 100%;
  flex-wrap: wrap;
  justify-content: space-between;
}

main .item {
  width: 30%;
  margin-bottom: 50px;
  background: #f5f5f5;
  overflow: hidden;
  position: relative;
  padding-bottom: 10px;
  display: flex;
  flex-direction: column;
  gap: 0px;
  justify-content: space-between;
}

main .item img,
.full_item img {
  width: 100%;
  border-radius: 10px 10px 0 0;
  transition: transform 500ms ease;
  cursor: pointer;
}

main .item img:hover,
.full_item img:hover {
  transform: scale(1.05);
}

main h2,
.full_item h2,
.full_item p,
main p,
.full_item b,
main b {
  margin: 10px 20px;
  color: #333;
}

main b,
.full_item b {
  color: #5fa36a;
}

footer {
  text-align: center;
  margin-top: 100px;
}